  <div id="app">
    <v-app>
      <v-main class="grey lighten-2">
        <v-row class="my-1" justify="center" no-gutters>
          <v-col
            class="px-1"
            style="max-width: 130px !important"
            align="center"
            outlined
            tile
            dense
            fixed
          >
            <div class="sticky">
              <v-card rounded="2" elevation="10" class="mr-2">
                <!-- <v-toolbar flat dense color="blue darken-4" dark>
                  <span class="title font-weight-regular">Control</span>
                </v-toolbar> -->
                <draggable
                  element="span"
                  v-model="controls"
                  :sort="false"
                  v-bind="dragOptions"
                  :group="{ name: 'items', pull: 'clone', put: false }"
                  :clone="handleClone"
                >
                  <transition-group
                    class="list-group overflow-y-auto"
                    three-line
                    style="
                      max-height: calc(100vh - 100px);
                      border-top: 10px solid #0d47a1;
                    "
                    tag="v-list"
                  >
                    <v-list-item
                      class="list-group-item grab"
                      v-for="element in controls"
                      :key="uuid(element)"
                      justify="center"
                    >
                      <v-list-item-title
                        class="d-flex justify-start"
                        :class="
                          element.fixed
                            ? 'fa fa-anchor'
                            : 'glyphicon glyphicon-pushpin'
                        "
                        v-text="element.Type"
                        aria-hidden="true"
                      ></v-list-item-title>
                    </v-list-item>
                  </transition-group>
                </draggable>
                <!-- <v-btn tile color="blue" outlined block>Thêm</v-btn> -->
              </v-card>
            </div>
          </v-col>
          <v-col style="max-width: 640px !important" outlined tile>
            <v-app-bar app elevation="0" class="blue darken-4" dense fixed>
              <v-toolbar flat dense color="blue darken-4" dark>
                <!-- <v-text-field
                v-model="nameTemplates"
                solo hide-details single-line></v-text-field> -->
                <!-- <v-text-field background-color="#00000055" loading="inputName.isChange" style="max-width: 200px;" @change="inputName.isChange = true " flat solo dense hide-details single-line placeholder="Tên mẫu"/> -->
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-text-field
                      v-bind="attrs"
                      v-on="on"
                      background-color="#00000000"
                      v-model="nameTemplates"
                      loading="false"
                      style="max-width: 200px"
                      dense
                      flat
                      solo
                      hide-details
                      single-line
                      placeholder="Tên mẫu"
                    />
                  </template>
                  Tên mẫu
                </v-tooltip>
                <v-spacer></v-spacer>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <v-btn
                      fab
                      small
                      v-bind="attrs"
                      v-on="on"
                      icon
                      @click="requestTemplates"
                    >
                      <v-icon>mdi-upload</v-icon>
                    </v-btn>
                  </template>
                  <span>Tải lên</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <jsonviewer
                        class="mr-3"
                        :nameTemplates="nameTemplates"
                        :data="templates"
                      ></jsonviewer>
                    </div>
                  </template>
                  <span>Xem json</span>
                </v-tooltip>
                <v-tooltip bottom>
                  <template v-slot:activator="{ on, attrs }">
                    <div v-bind="attrs" v-on="on" rounded>
                      <v-file-input
                        style="max-width: 30px"
                        accept="application/json"
                        prepend-icon="mdi-import"
                        hide-input
                        @change="importTemplates"
                      >
                      </v-file-input>
                    </div>
                  </template>
                  <span>Nhập json</span>
                </v-tooltip>
              </v-toolbar>
            </v-app-bar>
            <!-- overflow-y-auto"
              three-line
              style="max-height: calc(100vh - 100px)" -->
            <v-card class="pa-5">
              <div class="mx-2">
                <formtitle :data="templates"></formtitle>
                <draggable
                  v-model="templates.Part"
                  element="span"
                  @sort="handleSort"
                  :group="{ name: 'part' }"
                  :sort="true"
                  handle=".dragbar"
                >
                  <v-card
                    class="px-3 mt-2"
                    flat
                    outlined
                    v-for="(element, i) in templates.Part"
                    :key="i"
                  >
                    <v-toolbar flat tile class="dragbar" align="right">
                      <v-row>
                        <v-col cols="2"></v-col>
                        <v-col class="d-flex justify-space-around">
                          <!-- <v-icon>mdi-drag</v-icon> -->
                        </v-col>
                        <v-col cols="2" class="d-flex justify-end">
                          <v-btn
                            class="my-2 mx-0"
                            text
                            icon
                            color="red"
                            v-on:click="templates.Part.splice(--i, 1);"
                          >
                            <v-icon>mdi-delete</v-icon>
                          </v-btn>
                        </v-col>
                      </v-row>
                    </v-toolbar>
                    <session :data="element" :part="++i" :key="i"></session>
                  </v-card>
                </draggable>
                <div class="mt-2">
                  <v-btn
                    v-on:click="
                      templates.Part.push({
                        Name: 'THÔNG TIN',
                        Des: 'Mô tả',
                        Question: [],
                      });
                    "
                    color="blue"
                    outlined
                    block
                    >thêm trang</v-btn
                  >
                </div>
              </div>
            </v-card>
          </v-col>
        </v-row>
        <v-snackbar v-model="snackbar.snackbar" :timeout="snackbar.timeout">
          <v-row no-gutters>
            <v-col v-if="snackbar.status" cols="3"
              ><v-icon color="success">mdi-check-circle</v-icon></v-col
            >
            <v-col v-if="!snackbar.status" cols="3"
              ><v-icon color="error">mdi-cancel</v-icon></v-col
            >
            <v-col cols="7">{{ snackbar.text }}</v-col>
          </v-row>
        </v-snackbar>
        <!-- <v-row no-gutters>
          <v-col class="pa-2" outlined tile>
            <pre>{{ listString }}</pre>
          </v-col>
        </v-row> -->
      </v-main>
    </v-app>
  </div>
</template>

<script>
import session from "./components/Session";
import formtitle from "./components/Formtitle";
import jsonviewer from "./components/jsonviewer/JsonViewer";
import draggable from "vuedraggable";
import axios from "axios";

export default {
  name: "App",
  data: function () {
    return {
      snackbar: {
        status: false,
        snackbar: false,
        text: "",
        timeout: 2000,
      },
      nameTemplates: "qaform",
      controls: [
        {
          QID: "",
          Type: "Radio",
          Text: "Tiêu đề",
          Option: [
            {
              Text: "Tuỳ chọn 1",
            },
            {
              Text: "Tuỳ chọn 2",
            },
          ],
        },
        {
          GroupID: "",
          Type: "Checkbox",
          Text: "Tiêu đề CheckBox",
          Option: [
            {
              QID: "",
              Text: "Checkbox 1",
            },
          ],
        },
        {
          QID: "",
          Type: "TextBox",
          Text: "Tiêu đề",
          Holder: "Nhập câu hỏi",
        },
        // {
        //   QID: "",
        //   Type: "FileInput",
        //   Text: "Tiêu đề",
        // },
        {
          QID: "",
          Type: "TextArea",
          Text: "Tiêu đề",
          Row: "3",
          Holder: "Nhập nội dung",
        },
        {
          Type: "MarkTable",
          Text: "Tiêu đề MarkTable",
          Des: "Mô tả",
          ValueArray: [
            {
              Value: "1",
              Text: "Rất thấp",
            },
            {
              Value: "2",
              Text: "Thấp",
            },
            {
              Value: "3",
              Text: "Trung bình",
            },
            {
              Value: "4",
              Text: "Cao",
            },
            {
              Value: "5",
              Text: "Rất cao",
            },
          ],
          Topic: [
            {
              GroupID: "",
              Name: "Tiêu đề câu hỏi",
              Question: [
                {
                  QID: "",
                  Text: "1. Câu hỏi",
                },
              ],
            },
          ],
        },
      ],
      templates: {
        templateID: this.generateUUID(),
        logo_org:
          "https://lhu.edu.vn/ViewPage/LHUVN/_default/image/truong_dai_hoc_lac_hong_logo.png",
        name_org: "Trường Đại học Lạc Hồng",
        url_finishRedirect: "https://lhu.edu.vn",
        Title: "PHIẾU KHẢO SÁT",
        Des: "Mô tả",
        url_login: "",
        url_template: "",
        Part: [
          {
            Name: "THÔNG TIN CHUNG",
            Des: "",
            Question: [],
          },
        ],
      },
      loadingBtn: false,
    };
  },
  methods: {
    handleSort(){
      this.templates.Part.map((e, i) => {
        return {e,i};
      })
    },
    importTemplates(files) {
      if (files.length <= 0) {
        return false;
      }
      const fr = new FileReader();
      fr.onload = (e) => {
        const result = JSON.parse(e.target.result);
        this.templates = JSON.parse(JSON.stringify(result, null, 2));
      };
      fr.readAsText(files);
    },
    requestTemplates() {
      const token =
        "eyJ1aWQiOiIxMTgwMDAxNDgiLCJzaWQiOiJFMTcxOTczMzQwM0NCOTY4IiwiZXhwIjoyMDc1MjI2MjgwfQ.zHayA4r7ObvK9gtzHtOXfil04VJcrvmDgihv0yiKsEQ";
      const instance = axios.create({
        baseURL: "https://tapi.lhu.edu.vn/qa",
      });
      instance.defaults.headers.common["Authorization"] = token;
      instance
        .post("/Web_KiemTraTemplateID", {
          TemplateID: this.templates.templateID,
        })
        .then((response) => {
          if (response.data.data[0].error) {
            this.snackbar.status = false;
            this.snackbar.text = response.data.data[0].message;
            // console.log(response.data.data[0].message)
          } else {
            this.snackbar.status = true;
            instance
              .post("/Web_TemplateUpdate", {
                TemplateID: this.templates.templateID,
                TenKhaoSat: this.nameTemplates,
                jsonDataNew: JSON.stringify(this.templates),
              })
              .then((response) => {
                this.snackbar.text = response.data.data[0].message;
                console.log(response.data);
              })
              .catch((error) => {
                console.log(error);
              })
              .finally(() => {
                this.loadingBtn = false;
              });
            // console.log(response.data.data[0].message)
          }
          this.snackbar.snackbar = true;
        });
    },
    uuid(e) {
      if (e.uid) return e.uid;
      const key = Math.random().toString(16).slice(2);
      this.$set(e, "uid", key);
      return e.uid;
    },
    generateUUID() {
      const key = Math.floor(Math.random() * (1000000 - 1)) + 100000;
      return key;
    },
    handleClone(item) {
      // Create a fresh copy of item
      let cloneMe = JSON.parse(JSON.stringify(item));
      this.$delete(cloneMe, "uid");
      return cloneMe;
    },
  },
  components: {
    draggable,
    session,
    formtitle,
    jsonviewer,
  },
  computed: {
    dragOptions() {
      return {
        animation: 100,
        disabled: false,
        ghostClass: "ghost",
      };
    },
    listString() {
      return JSON.stringify(this.templates, null, 2);
    },
  },
};
</script>

